import React, { useState } from 'react'
import HomeIcon from '@material-ui/icons/Home';
import { AssignmentTurnedInOutlined, ExpandMore, FeaturedPlayListOutlined, PeopleAltOutlined, Search } from '@material-ui/icons';
import CloseIcon from '@material-ui/icons/Close';
import { Avatar, Button, Input, withWidth } from '@material-ui/core';
import './css/Header.css';
import Modal from 'react-responsive-modal';
import 'react-responsive-modal/styles.css';

function Header() {

  const [isModalOpen, setIsModalOpen] = useState(false);
  const [inputUrl, setInputUrl] = useState("");
  const Close = <CloseIcon />;

  return (

    
    <div className='uheader'>
      <div className='uheader-content'>
        <div className='logo'>
        <img src='./Images/logo/logo.png' alt='logo' />
        </div>
              <div className='icons'>
                <div className='icon'><HomeIcon /></div>
                <div className='icon'><FeaturedPlayListOutlined /></div>
                <div className='icon'><AssignmentTurnedInOutlined /></div>
                <div className='icon'><PeopleAltOutlined /></div>
            </div>
            <div className="uheader_input">
              <Search />
              <input type="text" placeholder="Search questions" />
            </div>
            <div className="uheader_Rem">
              <Avatar />
            </div>
            <div className="uheader_button">
              <Button onClick = {() => setIsModalOpen(true)}>Add Questions</Button>
              <Modal
                open={isModalOpen}
                closeIconIcon={Close} onClose ={()=>setIsModalOpen(false)}
                closeOnEsc 
                center
                closeOnOverlayClick={false}  
                styles={{
                  overlay:{
                    height:"auto",
                    background:"#000"
                  },
                }}       
              >
           <div className='modal_title'>
                  <h5>Add Question</h5>
                  <h5>Share Link</h5>
                </div>
                <div className='modal_info'>
                  <Avatar className='avatar' />
                  <div className='modal_scope'>
                    <PeopleAltOutlined />
                    <p>Public</p>
                    <ExpandMore />
                  </div>
                </div>
                <div className='modal_field'>
                  <Input type='text' 
                  style={{
                    width: "100%"
                   }}
                  placeholder="Start your question with 'What', 'How', 'Why', etc. "/>
                  <div style={{
                    display: "flex",
                    flexDirection: "column"
                  }}>
                    <input type='text'
                    value = {inputUrl}
                    onChange = {(e) => setInputUrl(e.target.value)}
                    style={{
                      margin: "5px 0",
                      border: "1px solid lightgray",
                      padding: "10px",
                      outline: "2px solid #000",
                      width: "100%"
                     }}
                    placeholder = "Optional: include a link that gives context" />
                    {inputUrl !== "" && <img 
                    style={{
                      height: "40vh",
                      objectFit: "contain"
                    }}
                    src={inputUrl} alt="displayimage"/>}
                  </div>
                </div>
                <div className='modal_buttons'>
                  <button className='cancel' onClick={() => setIsModalOpen(false)}>
                    Cancel
                  </button>
                  <button type='submit' className='add'>
                    Add Question
                  </button>
                </div>
           </Modal>
        </div>
      </div>
    </div>
  )
}
export default Header
